#routines-page {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

.single-routine {
  border: solid #cdb4db;
  border-radius: 12px;
  margin: 0.5rem;
  padding: 0.5rem;
  width: 25vw;
}

.single-routine:hover {
  box-shadow: 0 0 5px 0.1rem #cdb4db;
}

.routine-name {
  text-decoration: underline;
}
