#app-title {
  color: white;
  margin: 0 auto;
  margin-left: calc(50vw - 200px);
}

.sidebar {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #cdb4db;
  height: 80px;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  display: flex;
  justify-content: center;
  position: fixed;
  background-color: #cdb4db;
  width: 250px;
  height: 100vh;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #cdb4db;
  width: 100%;
  height: 80px;
}

span {
  margin-left: 16px;
}

#source-code {
  display: flex;
  flex-flow: row wrap;
  margin-top: 40vh;
}
